* {
 margin: 0;
}


.app {
 display: flex;
 justify-content: space-evenly;
 background-color: #f5f6fa;
 padding: 5px;
}
.graph {
    padding: 20px;
    margin: 0 auto;
   }
.barChart{
    height: 450px
}
   @media only screen and (max-width: 1000px) {
    .barChart {
      height: 300px
    }
  }
  @media only screen and (max-width: 1100px) {
    .barChart {
      height: 350px
    }
  }
.app__dropdown {
 width: 300px;
 background-color: white;
}

.app__header {
 display: flex;
 align-items: center;
 margin-bottom: 5px;
 justify-content: space-between;
}

.app__header > h1 {
 color: #fc3c3c;
 font-size: 20px;
}

.app__stats {
 display: flex;
 justify-content: space-between;
 flex-wrap: wrap !important;
 margin-bottom: 20px;
}

.app__left {
 flex: 0.9 1;
}

.app__information > h3 {
 color: #6a5d5d;
 font-weight: 400;
 font-size: 1.5rem;
 margin-bottom: 1rem;
}

.app__information > h3:last-of-type {
 margin-top: 0.5rem;
}

@media (max-width: 990px) {
 .app {
  flex-direction: column;
 }
}

.infoBox {
 flex: 1 1;
padding-bottom: 5px;
margin-top: 10px;
/* background-color: red !important; */
min-width: 200px !important;
}

.infoBox:not(:last-child) {
 margin-right: 10px;
}

.infoBox--selected {
 border-top: 10px solid greenyellow;
}

.infoBox--red {
 border-color: #a70b99;
}

.infoBox__cases--green {
 color: lightgreen !important;
}

.infoBox__cases {
 color: #cc1034;
 font-weight: 600;
 font-size: 1rem;
 margin-bottom: 5px;

}

.infoBox__total {
 color: #6c757d;
 font-weight: 700 !important;
 font-size: 0.8rem !important;
 margin-top: 5px !important;
}

.infoBox_font {
 font-family: 'Kantumruy', sans-serif;
 font-size: 1rem;
 font-weight: 700;
}

.covid_tables {
  margin-top: 20px;
  /* overflow-y: scroll; */
  color: #6a5d5d;
  background-color: white;
  min-height: 250px;
}

.covid_tables .tr {
  display: flex;
  justify-content: space-between;
}

.covid_tables .td {
  padding: 0.5rem;
  border: none;
}

.covid_tables tr:nth-of-type(odd) {
  background-color: #fff;
  color: #000;
}
.covid_tables tr:nth-of-type(even) {
  background-color: #f1f6fa;
  color: #000;
}

.map {
 height: 600px;
 background-color: white;
 padding: 0.5rem;
 border-radius: 4px;
 margin-top: 5px;
 box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
 position: relative;
}
.layerSwitcher{
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1000;
}
.map .leaflet-container {
 height: 100%;
 border-radius: 12px;
}

.info-flag img {
 width: 100px;
 border-radius: 5px;
}

.info-name {
 font-size: 20px;
 font-weight: bold;
 color: #555;
}

.info-container {
 width: 150px;
}

.info-flag {
 height: 80px;
 width: 100%;
 background-size: cover;
 border-radius: 8px;
}

.info-confirmed,
.info-recovered,
.info-deaths {
 font-size: 16px;
 margin-top: 5px;
}

