.map {
 height: 600px;
 background-color: white;
 padding: 0.5rem;
 border-radius: 4px;
 margin-top: 5px;
 box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
 position: relative;
}
.layerSwitcher{
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1000;
}
.map .leaflet-container {
 height: 100%;
 border-radius: 12px;
}

.info-flag img {
 width: 100px;
 border-radius: 5px;
}

.info-name {
 font-size: 20px;
 font-weight: bold;
 color: #555;
}

.info-container {
 width: 150px;
}

.info-flag {
 height: 80px;
 width: 100%;
 background-size: cover;
 border-radius: 8px;
}

.info-confirmed,
.info-recovered,
.info-deaths {
 font-size: 16px;
 margin-top: 5px;
}
